/* App.css */

/* Estilos para el banner */
.Header {
  background-color: #1100ff; /* Azul oscuro */
  padding: 10px 20px; /* Espaciado interno */
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Header-left {
  display: flex;
  align-items: center;
}

.Header-title {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: white;
}

.Header-logo {
  width: 40px; /* Ajusta el tamaño del logo */
  margin-right: 10px; /* Espaciado entre el logo y el texto */
}

.Navbar {
  display: flex;
  align-items: center;
}

.Navbar-links {
  list-style: none;
  margin: 0;
  padding: 0;
}

.Navbar-links li {
  margin-right: 20px; /* Espacio entre los elementos de la barra de navegación */
}

.Navbar-links li a {
  text-decoration: none;
  color: white;
}

/* Estilos para el formulario */
.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
}

.heading {
  text-align: center;
}

.form {
  margin-top: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.button {
  width: 100%;
  padding: 10px;
  background-color: #0056b3;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.button:hover {
  background-color: #0056b3;
}

.registro {
  color: green;
}

.failed {
  color: red;
}

.upload-btn-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
  margin-right: 10px; /* Espacio entre los botones y los inputs */
}

.btn {
  border: 2px solid gray;
  color: gray;
  background-color: white;
  padding: 8px 20px;
  border-radius: 8px;
  font-size: 20px;
  font-weight: bold;
  display: inline-block;
  cursor: pointer;
}

.upload-btn-wrapper input[type=file] {
  font-size: 100px;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}

/* eventgeneric.css */

.event-generic-container {
  margin: 0 auto;
  max-width: 1200px; /* Control the maximum width to ensure readability */
}

.heading {
  text-align: center;
  color: white; /* Dark gray for better readability */
  font-size: 2rem; /* Larger font size for headings */
  margin-bottom: 20px; /* Space below the heading */
}

.event-container,
.event-container2 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05); /* Slightly stronger shadow for depth */
  max-width: 800px;
  margin: 20px auto; /* Simplified margin declaration */
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.event-container:hover,
.event-container2:hover {
  background-color: rgba(0, 0, 0, 0.7); /* Darker on hover */
  transform: translateY(-5px); /* Lift effect on hover */
}

.event {
  width: 100%; /* Use 100% to make it responsive within the container */
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
  opacity: 1; /* Full opacity */
}

.event img {
  width: 100%; /* Full width within the container */
  border-radius: 4px;
}

.event h2 {
  font-size: 1.5rem;
  color: #4fd1c5; /* Softer blue for a modern look */
  margin-bottom: 5px;
}

.event p {
  font-size: 1rem;
  color: #718096; /* Soft gray for paragraphs */
}

.event button {
  background-color: #4fd1c5; /* Unified color theme with h2 */
  color: #ffffff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.event button:hover {
  background-color: #38b2ac; /* Darker shade on hover */
}

.container {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
  color: #ffffff; /* Set text color to white for better visibility on a dark gradient */
  font-family: "Orbitron", sans-serif; /* Futuristic font */
}

.cards-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;
  gap: 20px;
  position: relative;
  justify-content: start; /* Align cards to the left */
}

.card {
  background: rgba(255, 255, 255, 0.1); /* Semi-transparent white background */
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5); /* Enhanced shadow for floating effect */
  -webkit-backdrop-filter: blur(
    10px
  );
          backdrop-filter: blur(
    10px
  ); /* Apply blur to the background inside the card */
  border: 1px solid rgba(255, 255, 255, 0.2); /* Subtle white border */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.6);
}

.title {
  font-size: 22px;
  font-weight: 700; /* Bolder font for title */
  margin-bottom: 15px;
}

.event-name,
.price {
  font-size: 18px;
  margin-bottom: 10px;
}

.buy-button {
  background-color: #007bff; /* Primary button color */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.buy-button:hover {
  background-color: #0056b3;
}
h4 {
  font-family: "Orbitron", sans-serif;
  font-weight: 400; /* Adjust the weight as needed */
}
h3 {
  font-family: "Orbitron", sans-serif;
  font-weight: 700; /* Adjust the weight as needed */
}

p {
  font-family: "Orbitron", sans-serif;
  font-weight: 400; /* Adjust the weight as needed */
}
li {
  font-family: "Orbitron", sans-serif;
