.button-cool {
  background-color: #9a0bd3;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 8px;
  transition: background 0.3s, transform 0.3s;
  margin-bottom: 20px; /* Space between input groups */
  margin-top: 20px; /* Space between input groups */
}

.button-cool:hover,
.button-cool:focus {
  background-color: #19cccc;
  transform: scale(1.05);
}

.button-cool:active {
  transform: scale(0.95);
  background-color: #397d39;
}

.auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Full height of the viewport */
  width: 100%; /* Full width for center alignment */
}

.input-group {
  margin-bottom: 20px; /* Space between input groups */
  margin-top: 20px; /* Space between input groups */
  width: 100%; /* Adjust the width as needed, or keep it responsive */
  max-width: 360px; /* Maximum width of the input group for better control */
  display: flex;
  flex-direction: column;
  align-items: center; /* Center align the items */
}

.input-group label {
  font-weight: 600; /* Make labels bold */
  margin-bottom: 5px; /* Space between label and input */
  color: white; /* Dark grey color for text */
  align-self: flex-start; /* Align labels to the start of the flex container */
}

.input-group input {
  width: 100%; /* Adjust to take full width of the input group */
  padding: 8px 12px; /* Padding inside the input */
  border: 2px solid #ccc; /* Light grey border */
  border-radius: 4px; /* Rounded corners */
  font-size: 16px; /* Larger font size for better readability */
  transition: border-color 0.3s; /* Smooth transition for border color */
}

.input-group input:focus {
  outline: none; /* Remove default focus outline */
  border-color: #007bff; /* Blue border color on focus */
}
.input-group input {
  margin-bottom: 10px; /* Adjust the value as needed */
}

